.BrailleCell {
  background: transparent;
  border: none;
  padding: 0px 2px;
}

.BrailleCell svg {
  fill: transparent;
  height: 100%;
  stroke: black;
  stroke-width: 2px;
  width: 100%;
}

.BrailleCell:active svg,
.BrailleCell.BrailleCell-selected:active svg {
  fill: #888;
  stroke: #888;
}

.BrailleCell.BrailleCell-selected svg {
  fill: black;
}

.BrailleCharacter {
  border: 1px solid #999;
  border-radius: 4px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr 1fr 1fr;
  height: 240px;
  width: 160px;
}

.BrailleStream {
  margin: 10px 15px 0px;
}

.BrailleStream-input {
  display: flex;
  flex-direction: row;
  margin-bottom: 10px;
}

.BrailleStream-view {
  margin-left: 20px;
  font-size: 72px;
  font-weight: bold;
}

.BrailleStream-commands {
  margin-bottom: 10px;
}

.BrailleStream-output {
  font-size: 24px;
  min-height: 56px;
  white-space: pre-wrap;
}

.BrailleTable .table-responsive {
  border-top: none;
}
    
.BrailleTable th {
  text-align: center;
}

.BrailleTable td {
  text-align: center;
  font-size: 40px;
  font-weight: bold;
}

.Braille-content {
  padding: 0px 0px 20px;
}

